<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td><code v-pre>http://{s}.map.bdimg.com/onlinelabel/?qt=tile&amp;styles=pl&amp;x={x}&amp;y={y}&amp;z={z}</code></td>
<td><code v-pre>optional</code> 指定服务地址。</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code> 服务版权描述信息。</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 最小层级。</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td><code v-pre>18</code></td>
<td><code v-pre>optional</code> 最大层级。</td>
</tr>
<tr>
<td>projectionTransforms</td>
<td>Boolean|Object</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定投影变换参数。<strong>结构： { from: 'BD09', to: 'WGS84' }</strong></td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层的提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
