      data() {
        return {
          options: [
            {
              value: 'http://{s}.map.bdimg.com/onlinelabel/?qt=tile&x={x}&y={y}&z={z}&styles=pl&scaler=1&p=1',
              label: '百度矢量瓦片地图'
